@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;background-color:black;min-width:100%;max-width:100%;margin:0;flex:1 1}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}.navDiv{z-index:101;position:fixed;min-width:100%;max-height:5%;max-width:100%}.navBar{width:100%;z-index:102;list-style-type:none;margin:0;padding:0;overflow:hidden;background-color:black}.navElement{z-index:110;cursor:pointer;color:#f73939;float:right}li a{font-weight:bold;display:block;text-align:center;padding:16px;text-decoration:none}li.navDiv:hover{background-color:#e23e57;color:aliceblue}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.headOuter{
    min-width: 100%;
    max-height: 100%;
    min-height: 100vh;
    max-width: 100vh;
    background-color: #000;
    text-align: center;
    display: grid;
    place-items: center;
}

.particles{
    height: 100%;
    min-width: 100%;
    position: "absolute";
    top: 0;
    left: 0;
    -webkit-filter: blur(1.8px);
            filter: blur(1.8px);
    position: absolute;
    z-index: 1;
}

.nameDiv {
    /* background-image: linear-gradient(to bottom right, #e23e57, #a32a3c ); */
    background-color: black;
    position: relative;
    color: #fff;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    margin: 0 auto;
    z-index: 2;
    font-size: 5vh;
    width: 100%;
    height: 100%;
}

.nameDivWrapper{
    margin-top: 10%;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 50vh;
    height: 50vh;
    border-radius: 50%;
    background: linear-gradient(to right, #f73939 , #f63ffc);
    padding: 3px;
}

@media screen and (max-width: 992px) {
    .nameDivWrapper{
        width: 85vw;
        height: 85vw;
    }
}



.svgIntro{
    z-index: 100;
    margin-right: 1vh;
    margin-left: 1vh;
    width: 4vh;
    height: auto;
    fill: #f73939;
    cursor: pointer;
}

.svgIntro:hover {
    fill: #fff;
}
.aboutContainer{
width: 100%;
}

.aboutMeOuter{
    min-width: 100%;
    height: auto;
    max-width: 100%;
    background-image: linear-gradient(to bottom, black, #f53844);
    text-align: center;
    display: grid;
    place-items: center;
}

.aboutMeInner{
    background-image: linear-gradient(to bottom, #171616, black);
    width: 99%;
    height: 99%;
    border-radius: 25px;
    padding-bottom: 10%;
}

.profilePic{
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.imageWrapper{
    margin: 2%;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 20vh;
    height: 20vh;
    border-radius: 50%;
    background: linear-gradient(to bottom, white , white);
    display: block;
    padding: 3px;
}

.innterIntroPar{
    display: grid;
    place-items: center;
    padding-top: 5%;
}

.introMonologDiv{
    text-align: center;
    color: #d1d1d1;
    display: block;
    max-width: 55vw;
}


.terminalBar{
    width: 100%;
    z-index: 102;
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #2a2b2b;
    max-height: 8vh ;
}

.terminalWindowElement{
    font-size: 1.5rem;
    color: white;
    float: right;
}

.terminalName{
    padding-left: 2vh;
    padding-top: 2.5vh;
    font-size: 1rem;
    color: white;
    float: left; 
}

.technicalSkillDiv{
    width: 100%;
    text-align: center;
    display: grid;
    place-items: center;
}

.terminalHead{
   width: 70%;
   border-style: solid;
   border-color: #2a2b2b;
   border-width: 4px;
}

@media screen and (max-width: 992px) {
    .terminalHead{
        width: 90%;
    }
}

.terminalIcon{
    margin-right: 1vh;
    fill: white;
    width: 2vh;
    height: auto;
}

.skillsOuter{
    /* background-color: #f73939; */
    background-color: #42378f;
    background-image: linear-gradient(315deg, #42378f 0%, #f53844 74%); 
    padding-top: 10vh;
    padding-bottom: 5vh;
    min-width: 100%;
    max-width: 100%;
    height: auto;

}


@media screen and (max-width: 992px) {
    .introMonologDiv{
        max-width: 100vh;
    }
}


